.App {
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  overflow: hidden;
}

.nav-bg {
  z-index: 999;
  width: 100%;
  height: 80px;
  background-color: black;
  position: fixed;
}
#nav-container {
  margin: 0;
  padding-left: 5%;
}
.color-white {
  color: white;
  font-size: 18px;
}

.logo {
  font-size: 33px;
  cursor: pointer;
}

.nav-color-white {
  color: white;
}

.link {
  line-height: 33px;
  margin-left: 5px;
}

.App-mainV-swiper {
  width: 100%;
  height: 100vh;
}

.SwiperSlide {
  width: 100%;
  height: 100%;
  position: relative;
}

#mainV-App-1::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(../public/public_assets/postmalone.jpg);
  background-size: cover;
  background-position: center;
  filter: blur(3px);
}

#mainV-App-2::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(../public/public_assets/morganwallen.jpg);
  background-size: cover;
  background-position: center;
  filter: blur(3px);
}
#mainV-App-3::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(../public/public_assets/taylor.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 15%;
  filter: blur(3px);
}

.mainV-text {
  position: absolute;
  top: 50%;
  width: 35%;
  margin-left: 5%;
}
#mainV-text-t1 {
  font-size: 2.4vw;
  font-weight: 700;
}
#mainV-text-t2 {
  font-size: 1.4vw;
}
#mainV-text-t3 {
  margin-top: 10px;
  font-size: 0.8vw;
}

.youtubebox {
  position: absolute;
  top: 25%;
  right: 5%;
  width: 47%;
  height: auto;
  background-color: transparent;
}

/* list */
.list {
  position: relative;
  margin-top: 100px;
  width: 100%;
  padding: 0 5%;
  height: 100%;
  /* overflow: hidden; */
}
.list .title {
  font-size: 38px;
  font-weight: bold;
}
.list .MusicBtn {
  position: absolute;
  font-size: 22px;
  margin-left: 5px;
  right: 5%;
  opacity: 0.7;
}
.list .MusicBtn:hover {
  opacity: 1;
}

.list .PrevBtn {
  right: 8%;
}
.list .NextBtn {
  /* opacity: 1; */
}

.list .Music-list {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.Music-Content {
  margin-top: 30px;
  width: 16%;
  height: 100%;
}
#album-img {
  position: relative;
  width: 100%;
  height: 100%;
}
#album-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0;
  transition: all 0.2s;
  cursor: pointer;
}

#album-img:hover #album-bg {
  opacity: 0.3;
}

.Music-Content img {
  width: 100%;
  height: auto;
}

.Music-Content .t1 {
  /* 말줄이기 */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  margin-top: 10px;
  font-size: 20px;
  font-weight: 700;
}
.Music-Content .t2 {
  font-size: 14px;
  opacity: 0.8;
}

.Modal {
  position: absolute;
  top: -70%;
  left: 0;
  z-index: 100;

  width: 100%;
  height: 150vh;
  overflow: hidden;
}
#Modal-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 800px;
  background-color: black;
  opacity: 0.6;
}
#Modal-box {
  position: absolute;
  transform: translateX(-50%);

  top: 10%;
  left: 50%;
  width: 50%;
  height: auto;

  background-color: rgb(54, 54, 54);
}

footer {
  opacity: 0.6;
  margin-top: 100px;
  color: white;
  padding: 0 5%;
  width: 100%;
  height: 100%;
}

.footer-line {
  margin: 0 auto;
  width: 100%;
  border-top: 2px solid white;
  margin-bottom: 10px;
}

