.mainV-y2k {
  position: relative;
  width: 100%;
  height: 100vh;
}
.mainV-y2k::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(../public/public_assets/avril.jpg);
  background-position: 50% 5%;
  background-repeat: no-repeat;
  background-size: 100%;

  filter: blur(3px);
}

